import Vue from 'vue';
import Router from 'vue-router';
import TabNetMonitor from '@/pages/TabNetMonitor';

Vue.use(Router);

export default new Router({
  base: '/netInfo/',
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'TabNetMonitor',
      component: TabNetMonitor,
    },
  ],
});



